
.book__container{
	display: flex;
	cursor: pointer;
	flex-direction: column;
	justify-content: space-between;
}
.book__container img{
	padding: 10px;
	max-height: 30rem;
	max-width: 20rem;
	object-fit: contain;
	align-self: center;
}

.book__containerDescAuthor{
	font-size: 18px;
	color: brown;
	word-wrap: break-word;
}

.book{
	width: 23rem;
	color: grey;
	word-wrap: break-word;
	max-height: 700px;
	background-color: rgb(193 188 22 / 35%);
	margin: 0.5rem;
	padding: 1rem;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-between;
	border-radius: 10px;
	box-shadow: 1px 1px lightgrey;
}
.book button{
  cursor: pointer;
  height: 50px;
  border-radius: 10px;
  font-size: 20px;
}

.book__button{
	display: flex;
	justify-content: space-around;
}

.book:hover{
	transform: scale(1.01);
	transition: transform 0.5s;
}
.book a{
	color: black;
	text-decoration: none;
}
@media only screen and (max-width: 600px) {
 	.book__containerDesc{
		display: none; 		
 	}
 	.author{
 		display: none;
 	}
 	.book{
 		width: 15rem;
 	}
 	.book__container img{
 		max-width: 10rem;
 	}
}