.cartitem {
  color: black;
  background-color: lightgreen;
  display: flex;
  border-top-left-radius: 50px;
  border-bottom-left-radius: 50px;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  height: 7em;
  font-size: 1.5em;
  margin: 10px;
}
.cartitem button {
  font-size: 1.5rem;
  color: white;
  background-color: red;
  height: 30%;
  border-radius: 5px;
  cursor: pointer;
}
.cartitem img {
  width: 6rem;
  border-radius: 50px;
}
