.cart {
  background-image: linear-gradient(180deg, #ffff0036, white);
}
.cart__placeOrder {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.cart__placeOrderEnabled {
  margin-left: 10px;
  background-color: #ff8d00d1;
  color: white;
  padding: 10px;
  text-decoration: none;
  font-size: 1.25rem;
  border-radius: 10px;
}
.cart__placeOrderDisabled {
  display: none;
}
